#root {
  height: 100vh;
}
*, *::before, *::after {
  box-sizing: inherit;
}
body {
  box-sizing: inherit;
}
.content {
  font-family: Roboto, "-apple-system", "system-ui", BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
  color: #333333;
  line-height: 1.4;
  font-size: 14px;
  padding: 8px;
  height: 100vh;
  max-height: 100%;
}
.icons-contaniner {
  display: flex;
  padding: 16px;
  flex-wrap: wrap;
  .icon-container {
    display: flex;
    border-radius: 4px;
    width: 124px;
    height: 96px;
    justify-content: center;
    align-items: center;
    margin: 8px 4px;
    position: relative;
    border: 1px solid #f5f5f5;
    .icon-title {
      position: absolute;
      left: 0;
      top: 100%;
      width: 100%;
      text-align: center;
      font-family: Consolas, monospace;
      font-size: 11px;
    }
  }
}
.table-text-right {
  width: 100%;
  text-align: right;
}
.addon-notes-container pre {
  font-family: Consolas, monospace;
}
hr {
  background: none;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #dcdcdc;
  border-left: 0;
  border-right: 0;
  margin: 24px 0;
}
.u-padding {
  padding: 16px;
}
.u-padding-top {
  padding-top: 16px;
}
.u-margin-top {
  margin-top: 16px;
}
.u-padding-bottom {
  padding-bottom: 16px;
}
.u-flex {
  display: flex;
}
.u-padded {
  padding: 16px;;
}